import { Popconfirm } from "antd";
import { useCallback, useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { Fn } from "../../../helper/function";
import { PaginationRequest, TableColumn } from "../../../models/baseModel";
import { MailTemplate } from "../../../services/adminService";
import { MyBox } from "../../core/MyBox";
import { MyButton } from "../../core/MyButton";
import { OrderByModel, PageInfoModel } from "../../core/TableModel";
import TableComponent from "../../core/TableNewComponent";
import { UIFormatDateTimeVNComponent } from "../../core/UI_FormatComponent";

export default function ManageTemplateMailIndexComponent() {
    const navigate = useNavigate();
    const column: any = [
        new TableColumn({ title: "STT", dataIndex: "stt", key: "stt", disabled: true }),
        new TableColumn({ title: "Mã", dataIndex: "code", key: "code" }),
        new TableColumn({ title: "Tiêu đề", dataIndex: "title", key: "title" }),
        new TableColumn({ title: "App name", dataIndex: "appName", key: "appName" }),
        new TableColumn({
            title: "Ngày tạo", dataIndex: "createdAt", key: "createdAt", render: (data: string) => {
                return (<UIFormatDateTimeVNComponent value={data} />);
            }
        }),
        new TableColumn({
            title: "Ngày cập nhật", dataIndex: "updatedAt", key: "updatedAt", render: (data: string) => {
                return (<UIFormatDateTimeVNComponent value={data} />);
            }
        }),
        new TableColumn({
            title: "Action", key: "action", disabled: true, render: (data: string, row: any) => {
                return (
                    <div>
                        <MyButton
                            onClick={() => {
                                navigate(`/admin/template/edit/${row.key}`)
                            }}
                            value="Chỉnh sửa"
                            type="link" />

                        <Popconfirm
                            title="Bạn có muốn xóa không?"
                            onConfirm={() => {
                                handleClickDelete(row.key)
                            }} >
                            <MyButton
                                value="Xóa"
                                type="link" />
                        </Popconfirm>
                    </div>
                )
            }
        })
    ];
    const [pageInfo, setPageInfo] = useState<PageInfoModel>(new PageInfoModel());
    const [orderBy, setOrderBy] = useState<OrderByModel>(new OrderByModel());
    const [data, setData] = useState([]);
    const [isLoading, setIsLoading] = useState(false);

    const loadData = useCallback(() => {
        setIsLoading(true);
        let pagination = new PaginationRequest(pageInfo.pageNumber, pageInfo.pageSize, orderBy);
        MailTemplate.GetList(pagination, (data: any) => {
            setPageInfo(new PageInfoModel(data));
            let row = data.data.data.pagedData.map((data: any, index: number) => {
                return {
                    stt: index + 1,
                    key: data.key,
                    code: data.code,
                    title: data.title,
                    appName: data.apps.map((data: any) => {
                        return data.name
                    }).join(", "),
                    createdAt: data.createdAt,
                    updatedAt: data.updatedAt
                }
            })
            setData(row);
        }, (err: any) => {
            console.log(err);

        }, () => {
            setIsLoading(false);
        })
    }, [pageInfo.pageSize, pageInfo.pageNumber, orderBy]);

    useEffect(() => {
        loadData();
    }, [loadData]);

    function handleClickDelete(key: string) {
        setIsLoading(true);
        MailTemplate.Detail(key, (data: any) => {
            MailTemplate.Delete(key, Fn.GetTimestamp(data), (data: any) => {
                Fn.PrintMsgSuccess(data);
                loadData();
            }, () => {
                setIsLoading(false);
            })
        }, () => {
            setIsLoading(false);
        })
    }

    return (
        <MyBox
            title="Quản lý template mail"
            isLoading={isLoading}>
            <Link to={"/admin/template/add"}> <MyButton value="Thêm mới" /></Link>
            <TableComponent
                pageInfo={pageInfo}
                setPageInfo={setPageInfo}
                orderBy={orderBy}
                setOrderBy={setOrderBy}
                column={column}
                dataSource={data}
                loading={isLoading}
                loadData={loadData} />
        </MyBox>
    );
}